import React,{useState} from 'react';
import {Card} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css'
const TkTe =()=>{
  let status="";
  const [sq,setSq] = useState(Array(9).fill(null))
  const [xo,setXo] = useState(true)
  const hdClick =(i)=>{
    if (Winner(sq)||sq[i]){
      return true
    }
    console.log(sq,'hi',Winner(sq),'hi',i)
    sq[i] = xo?'X':'0';
    setSq(sq)
    setXo(!xo)
  }
  const renderSq =(i)=>{
    return (<Squares val ={sq[i]} onClick ={()=>hdClick(i)}/>)
  }
  const st = Winner(sq);
  if(st){
    status = `Winner ${st}`
  }else{
    status = `Next player ${xo?'X':'O'}`
  }
  return (
    <div>
      <div className='row'>
        {renderSq(0)}
        {renderSq(1)}
        {renderSq(2)}
      </div>
        <div className='row'>
        {renderSq(3)}
        {renderSq(4)}
        {renderSq(5)}
      </div>
        <div className='row'>
        {renderSq(6)}
        {renderSq(7)}
        {renderSq(8)}
      </div>
      <div>{status}</div>
    </div>
  )
}
export default TkTe;
const Squares =({val,onClick})=>{
   return(<Card style ={{display:'flex',padding:'0.5rem',
                          width:'3rem',height:'2rem',border:'1px solid black'}}
                          onClick ={onClick}>
                          {val}
    </Card>)
}
const Winner =(sq)=>{
  let wP =[[0,1,2],[3,4,5],[6,7,8],[0,3,6],[1,4,7],[2,5,8],[0,4,8],[2,4,6]
  ]
  for(var i =0;i<wP.length;i++){
    const [a,b,c] = wP[i]
    if(sq[a] && sq[a]=== sq[b] && sq[a]===sq[c]){
      return sq[a]
    }
  }
  return null
}